export function useGetBoxShadow(config: any) {
  const boxShadowStyle: any = {
    boxShadow: `${config.boxShadowX}px ${config.boxShadowY}px ${config.boxShadowBlurR}px ${config.boxShadowSpreadR}px ${
      config.boxShadowColor || 'rgba(0,0,0,0)'
    }`,
  };
  return {
    boxShadowStyle,
  };
}
// 渐变色背景
export function useGetLinearBackGround(config: any) {
  let LinearBackGroundStyle: any = {};
  if (config.color1 && config.color2) {
    LinearBackGroundStyle = {
      background: `linear-gradient(${config.deg}deg,${config.color1},${config.color2})`,
    };
  } else {
    LinearBackGroundStyle = {
      background: 'none',
    };
  }

  return {
    LinearBackGroundStyle,
  };
}
//
export function useGetBackGround(config: any) {
  const { backgroundRadioGroup: type } = config;
  let backgroundStyle = {};
  // 选择背景色
  if (type === 1) {
    backgroundStyle = {
      background: `${config.backgroundColor}`,
    };
  }
  // // 背景图片
  // if (type === 2) {
  //     backgroundStyle = {
  //         backgroundImage: config.backgroundImage
  //     }
  // }
  // 渐变色
  if (type === 3) {
    if (config.color1 && config.color2) {
      backgroundStyle = {
        background: `linear-gradient(${config.deg}deg,${config.color1},${config.color2})`,
      };
    } else {
      backgroundStyle = {
        background: 'none',
      };
    }
  }
  return {
    backgroundStyle,
  };
}
